import React, { useState, useEffect } from 'react';
import { useNavigate } from "react-router-dom";
import '../../styles/About/MotiModal.css';

function MotiModal() {
    const navigate = useNavigate();

    const [companyName, setCompanyName] = useState("");
    const [inputValue, setInputValue] = useState("");
    const [ip, setIp] = useState("");

    useEffect(() => {
        fetch('/cfg/ip.cfg')
            .then(res => res.json())
            .then(data => {
                setIp(data.pc);
                console.log("ip fetched:", data.pc);
            })
            .catch(err => console.error('ip.cfg  :', err));
    }, []);

    useEffect(() => {
        if (!ip) return;
        fetch(`${ip}/moti`)
            .then(res => res.json())
            .then(data => {
                if(data && data.name) setCompanyName(data.name);
            })
            .catch(err => console.error("  :", err));
    }, [ip]);

    const submit = async () => {
        try {
            const response = await fetch(`${ip}/moti?companyName=${inputValue}`, {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                }
            });

            const result = await response.text();
            if(result === "success") {
                alert(" .");
                setCompanyName(inputValue);
                setInputValue("");
                navigate("/", { state: { updatedCompany: inputValue } });
            }else {
                alert(" : " + result);
            }
        }catch (error){
            console.error("  :", error);
            alert("  ");
        }
    };

    const closeDialog = () => {
        navigate("/", { state: { updatedCompany: companyName } });
    };

    return (
        <div className="moti-dialog-overlay">
            <button className="dialog-close" onClick={closeDialog}>x</button>
            <div className="moti-dialog-box" onClick={(e) => e.stopPropagation()}>
                <div className="moti-first-top">
                    <h3>Q.  ?</h3>
                </div>

                <div className='emph-text-container'>
                     <span className='emph-text'>UI/UX</span>       .<br />
                      ,         ,<br />
                                 .<br />
                           ,   <span className='emph-text'>{companyName}</span>()  .
                </div>

                <div className="moti-first-top">
                    <h3>TO.  </h3>
                </div>

                <div className="input-button-row">
                    <input type='text' className='emph-input' value={inputValue} onChange={(e) => setInputValue(e.target.value)} />
                    <button className='emph-summit' onClick={submit}></button>
                </div>
            </div>
        </div>
    );
}

export default MotiModal;